html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  font-size: 2rem;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

body {
  background-image: url("../../src/assets/gamesBackground.png");
  background-color: #542E7E;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
}

.header__container {
  background-color: #3D0750;
  align-items: center;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 29%;
}

.header__back-button {
  background-color: #F9D44B;
  margin-left: 10%;
  color: white;
  max-height: 35px;
  max-width: 150px;
  font-family: "Acme", sans-serif;
  font-size: 2.5rem;
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.75);
}

.header__title {
  font-size: 32px;
  font-family: "Acme", sans-serif;
  color: white;
  align-self: center;
  text-align: center;
}

.header__instructions-button {
  margin-right: 10%;
  background-color: #7DB778;
  color: white;
  max-height: 35px;
  max-width: 150px;
  font-family: "Acme", sans-serif;
  font-size: 2.5rem;
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.75);
}

.steps__card-container {
  display: grid;
  grid-gap: 20px 100px;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, auto);
  margin-top: 1%;
  margin-left: 5%;
  margin-right: 5%;
}

.card {
  box-sizing: border-box;
  max-width: 250px;
  padding: 0.45em 0.5em;
  border: 4px solid #D4BFEB;
  border-radius: 5px;
  background-color: #D4BFEB;
}

.card__image-container {
  max-width: 220px;
  max-height: 230px;
  position: relative;
}

.card__image {
  width: 220px;
  padding-left: 4px;
  object-fit: contain;
  line-height: 0;
}

.card__text-container {
  width: 100%;
  height: 55px;
  margin-top: 12px;
  background-color: #D8E4EC;
  border-radius: 10px;
  border-style: dashed;
  border-color: #3D0750;
}
.card__text-container:hover {
  background-color: #F9D44B;
  cursor: move;
}

.footer {
  max-height: 0%;
}

.choose-steps__container {
  display: grid;
  grid-template-columns: auto auto auto auto auto auto auto auto;
  grid-gap: 20px 80px;
  margin: 5%;
}

.choose-steps__pending-steps-container {
  max-height: fit-content;
  padding: 5px;
  font-size: 2rem;
  background-color: #F9D44B;
  color: black;
  border-radius: 5px;
  border: none;
  font-weight: bold;
  font-family: "Acme", sans-serif;
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.75);
  transition: all 0.4s;
}
.choose-steps__pending-steps-container:hover {
  transform: scale(1.2);
}

.choose-steps__pending-steps-title {
  align-self: center;
}

@media screen and (max-width: 768px) {
  body {
    min-height: 100%;
  }
  .header__container {
    grid-gap: 0%;
  }
  .header__back-button {
    margin: 0;
  }
  .header__title {
    font-size: 32px;
    align-self: center;
  }
  .steps__card-container {
    grid-template-columns: repeat(2, 1fr);
    position: relative;
    margin: 10%;
  }
  .choose-steps__container {
    grid-template-columns: repeat(2, 1fr);
    position: relative;
  }
}
@media screen and (max-width: 1400px) and (min-width: 768px) {
  .header__container {
    grid-gap: 6%;
  }
  .steps__card-container {
    grid-template-columns: repeat(3, 1fr);
  }
  .choose-steps__container {
    grid-template-columns: repeat(4, 1fr);
    position: relative;
  }
}